.footer-container {
    height:17.8%;
    width:100%;
    overflow: hidden;
    display:flex;
    justify-content: center;
}
.footer-parents {
    width:100%;
    height:100%;
    max-width:1920px;
}
.footer-parents img {
    max-width: 100%;
    height:130px;
}
@media only screen and (max-width: 1110px){
    .footer-parents{
        display:flex;
        align-items:flex-end;
    }
}